  width: 78px;
  height: 2px;
  background: #343c3f;
}

.s-line__left {
  margin-right: 2px;
}

.s-line__left:after {
  content: '';
  position: absolute;
  width: 11px;
  height: 2px;
  transform: rotate(122deg);
  background: #343c3f;
  right: -7px;
  top: -4px;
}

.s-line__right {
  margin-left: 2px;
}

.s-line__right:before {
  content: '';
  position: absolute;
  width: 11px;
  height: 2px;
  transform: rotate(122deg);
  background: #343c3f;
  left: -7px;
  top: 4px;
}

