.cube-block {
  text-align: center;
  position: relative;
  width: 100%;
  height: 100%;
  transform: rotateX(-15deg) rotateY(15deg);
  transform-style: preserve-3d;
  animation: rotateCube 7s infinite linear;
  transform-style: preserve-3d;
  transform: rotateX(-15deg) rotateY(15deg);
  animation: rotateCube 7s infinite linear;
  -webkit-transform-style: preserve-3d;
  -webkit-transform: rotateX(-15deg) rotateY(15deg);
  -webkit-animation: rotateCube 7s infinite linear;
}

.cube-wrapper {
  perspective: 1000px;
  -webkit-perspective: 1000px;
  width: 250px;
  height: 250px;
  text-align: center;
  display: inline-block;
}

.cube {
  position: absolute;
  /*border: 1px solid #fff;*/
  background: #FFF;
  opacity: .85;
  background-size: auto 100%;
}

.cube-front {
  width: 250px;
  height: 250px;
  -webkit-transform: translate3d(0, 0, 125px);
  transform: translate3d(0, 0, 125px);
  background-color: #000;
}

.cube-back {
  width: 250px;
  height: 250px;
  -webkit-transform: rotateY(180deg) translate3d(0, 0, 125px);
  transform: rotateY(180deg) translate3d(0, 0, 125px);
  background-color: #000;
}

.cube-top {
  width: 250px;
  height: 250px;
  -webkit-transform: rotateX(90deg) translate3d(0, 0, 125px);
  transform: rotateX(90deg) translate3d(0, 0, 125px);
  background-color: #000;
}

.cube-bottom {
  width: 250px;
  height: 250px;
  -webkit-transform: rotateX(-90deg) translate3d(0, 0, 125px);
  transform: rotateX(-90deg) translate3d(0, 0, 125px);
  background-color: #000;
}

.cube-left {
  width: 250px;
  height: 250px;
  left: 50%;
  margin-left: -125px;
  -webkit-transform: rotateY(-90deg) translate3d(0, 0, 125px);
  transform: rotateY(-90deg) translate3d(0, 0, 125px);
  background-color: #000;
}

.cube-right {
  width: 250px;
  height: 250px;
  left: 50%;
  margin-left: -125px;
  -webkit-transform: rotateY(90deg) translate3d(0, 0, 125px);
  transform: rotateY(90deg) translate3d(0, 0, 125px);
  background-color: #000;
}

@-webkit-keyframes rotateCube {
  0% {
    -webkit-transform: rotateY(0deg) rotateX(0deg);
  }
  100% {
    -webkit-transform: rotateY(-360deg) rotateX(-360deg);
  }
}

@keyframes rotateCube {
  0% {
    transform: rotateY(0deg) rotateX(0deg);
  }
  100% {
    transform: rotateY(-360deg) rotateX(-360deg);
  }
}

